body {
  margin: 0;
  padding: 0;
  background: url(/StarWars-Starships/static/media/star3.4d86a4ed.jpg) no-repeat center fixed;
  background-size: cover;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

 @font-face {
  font-family: 'STAR WARS FONT';
  font-style: normal;
  font-weight: normal;
  src: local('STARWARS FONT'), url(/StarWars-Starships/static/media/star-wars-font.b16e1446.woff) format('woff');
  }
  
  h1 {
    font-family: 'STAR WARS FONT';
    font-weight: 200;  
    text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
      
 }

    
  h2 {
    font-family: 'STAR WARS FONT';
    font-weight: 100;
    font-size: 2rem;
    color:rgb(247, 252, 0);    
  }
  
  p {
    /* font-family: 'STAR WARS FONT'; */
    font: 1em sans-serif;
    text-align: left;
    color: white;
  }
  
  strong {
    /* text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white; */
    font-weight: 100;
  } 



